@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&diplay=swap);
header {
  height: 100vh;
  padding-top: 3rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}


/*==================CTA=====================*/

.cta{
  margin-top: 2.5rem;
  display: -webkit-flex;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  -webkit-justify-content: center;
          justify-content: center;
}

/*==================HEADER-SOCIALS=====================*/

.header__socials {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/*==================ME=====================*/
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 1rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 1rem 3rem 3rem;
}
/*==================SCROLL-DOWN=====================*/

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/

@media screen and (max-width: 1024px) {
  .header {
    height: 68vh;
  }
}

/* ===================MEDIA QUERIES (SMALL DEVICES) =============================*/

@media screen and (max-width: 600px) {
  .header {
    height: 100vh;
  }
  .header__socials, .scroll__down {
    display: none;
}
}







nav {
  background: rgb(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 2rem;
  display: -webkit-flex;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: -webkit-flex;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgb(0, 0, 0, 0.4);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}
.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary),
 transparent
);
  display: grid;
  place-items: center;
}
.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}
.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.about__icon {
  background: var(--color-primary); 
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about__card h5 {  
  font-size: 0.95rem;
}
.about__card small {  
  font-size: 0.7rem;
  color: var(--color-light); 
}
.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light); 
}

/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/

@media screen and (max-width: 1024px) {
.about__container {
  grid-template-columns: 1fr;
  grid-gap: 0;
  gap: 0;
}
.about__me {
  width: 50%;
  margin: 2rem auto 4rem;
}
.about__content p {
  margin: 1rem 0 1.5rem;
}
}
/* ===================MEDIA QUERIES (SMALL DEVICES) =============================*/

@media screen and (max-width: 600px) {
.about__me {
  width: 65%;
  margin: 0 auto 3rem; 
}
.about__cards {
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
}
.about__content {
  text-align: center;
}
.about__content p {
  margin: 1.5rem 0;
}
}






