section#portfolio {
  margin-top: 0rem;
}
.block-tabs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  grid-gap: 7rem;
  gap: 7rem;
}
 
.tabs {
  border-color: var(--color-primary-variant);
  background: transparent;
  color: var(--color-primary);
  padding: 10px;
  border-radius: 2rem;
  border: 1px solid;
  transition: var(--transition);
  width: 22rem;
  height: 3rem;
  text-align: center;
}
.active-tabs {
  background: var(--color-primary);
  color: var(--color-bg);
  padding: 10px;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
  width: 22rem;
  height: 3rem;
  text-align: center;
  transition-property: border background;
  transition: 0.5s all ease-in-out;
}
.active-tabs:hover, 
.tabs:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
  color: var(--color-primary);
 }
.tabs:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}
.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
  background: var(--color-primary);
}
.content {
  display: none;
}
.active-content {
  display: grid;
  grid-template-columns: repeat (3, lfr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
  margin-top: 30px;
}
